/*----------  article
------------------------------------------------------------------------------*/

.article {
    width: 100%;
    position: relative;
    padding: 25px 0;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    border-bottom: 1px solid #F3F3F3;
    &--left {
        float: left;
        width: 50px;
        display: flex;
        position: relative;
        &:hover {
            & ~ .article--right {
                .article--title a {
                    color: $blue;
                }
            }
        }
    }
    &--thumbnail {
        background-color: $greyLight;
        background-size: cover;
        background-repeat: no-repeat;
        background-position: center center;
        border-radius: 50%;
        height: 50px;
        width: 50px;
        align-self: center;
        &:before {
            position: absolute;
            top: 0;
            left: 0;
            width: calc(100% + 10px);
            height: 100%;
            content: '';
            display: block;
        }
    }

    &--right {
        display: inline-block;
        float: left;
        margin-left: 10px;
        width: calc(100% - 60px);
    }
    &--category {
        margin: 0;
        color: inherit !important;
        font-size: 16px;
        font-weight: 600;
        text-transform: lowercase;
        display: inline-block;
        position: relative;
        //overflow: hidden;
        &:before {
            content: '';
            display: block;
            position: absolute;
            bottom: -2px;
            width: 100%;
            height: 2px;
            border-bottom: 2px dotted currentColor;
            //background-color: currentColor;
            transition: all, .15s;
            //left: 50%;
            transform: translateX(-50%);
            opacity: 0;
        }
        &:hover {
            &:before {
                transform: translateX(0);
                opacity: 1;
            }
        }
    }
    &--title {
        color: #54595F;
        font-size: 24px;
        overflow: hidden;
        text-overflow: ellipsis;
        font-weight: 700;
        max-width: 60ch;
        //transition: color, 0.1s, cubic-bezier(0,.01,1,1);
        &:hover {
            color: $blue;
        }
        a {
            color: inherit;
            font-weight: inherit;
        }
    }
    &--author {
        margin-right: 3px;
    }
    &--info {
        font-size: 14px;
        line-height: 24px;
        color: #8F8F8F;
        cursor: default;
        &Separator {
            margin: 0 3px;
            display: inline-block;
        }
        a {
            font-weight: 400;
            font-size: 14px;
            line-height: 24px;
            color: #8F8F8F;
            padding: 3px 0 0 0;
            text-decoration: none;
            border-bottom: 1px solid #F3F3F3;
            letter-spacing: 0.5px;
            &:hover {
                color: #3D3D3D;
            }
        }
    }

    &--newDot {
        display: none;
        width: 10px;
        height: 10px;
        border-radius: 50%;
        position: absolute;
        background-color: $yellow;
        top: 50%;
        left: 0;
        transform: translate(-30px, -50%);
        cursor: pointer;
        .article--new & {
            display: block;
        }
    }

    &--content {
        width: 100%;
        flex-grow: 1;
        margin-top: 50px;
        color: #54595F;
        p {
            font-size: 18px;
            line-height: 26px;
            &:first-of-type {
                text-indent: 60px;
            }
        }
        strong {
            font-weight: 600;
        }
        img {
            margin: 40px auto;
            display: block;
            user-select: none;
            max-width: 100%;
            height: auto;
            
        }
        h3 {
            font-size: 22px;
            font-weight: 600;
            margin-top: 40px;
        }
        p, iframe, h3 {
            margin-bottom: 20px;
        }
    }

}

.filter-info {
    padding: 30px 0 10px 2px;
    opacity: .6;
    user-select: none;
}
