/*----------  ie fix
------------------------------------------------------------------------------*/
.chromeframe {
	width: 100%;
	position: absolute;
	top: 0;
	left: 0;
	text-align: center;
	background: #ccc;
	color: black;
	padding: 0.2em 0;
}
.no-js {
	.top--banner {
		.wrapper {
			display: block;
			margin: 0 auto;
			left: 0;
			position: absolute;
		}
	}
	.main-form {
		margin-top: -71px;
	}
	.lmr {
		left: 0;
		.main--topItem {
			display: block;
			&.active {
				&:after {
					display: none;
				}
			}
		}
	}
	.results {
		.main--top-line {
			h1 {
				left: 0;
			}
		}
	}
}
