/*----------  article
------------------------------------------------------------------------------*/

.newsletter-section {
    width: 100%;
    padding: 90px 30px;
    border-radius: 5px;
    text-align: center;
    margin: 60px auto 40px;
    background-image: url("../images/newsletter_bg.jpg");
    background-position: 0 0;
    background-repeat: no-repeat;
    position: relative;
    animation: bgsize, 10s;
    @keyframes bgsize {
        0%, 100% {
            background-size: 100%;
        }
        50% {
            background-size: 160%;
        }
    }
    &:before {
        position: absolute;
        top: 0; right: 0; bottom: 0; left: 0;
        width: 100%;
        height: 100%;
        display: block;
        content: '';
        background-color: rgba($black, .4);
    }
    &--form {
        display: inline-block;
        border-radius: 5px;
        margin-bottom: 30px;
        position: relative;
        overflow: hidden;
    }
    p {
        color: $white;
        position: relative;
        font-size: 16px;
        text-indent: 0 !important;
        margin-bottom: 0!important;
    }
    input,
    button {
        font-size: 16px;
        font-family: inherit;
        border: none;
        height: 40px;
        padding: 0 15px;
        margin: 0;
        display: block;
        float: left;
    }
    input {
        background-color: rgba($white,0.99);
        font-weight: 400;
        color: #555;
        @include placeholder {
            color: lighten(#456083, 10);
        }
        &:focus {
            @include placeholder {
                opacity: 0;
            }
        }
    }
    button {
        background-color: #58C9B9;
        color: $white;
        &:hover {
            background-color: #F6B352;
        }
    }
}
