/*----------  article
------------------------------------------------------------------------------*/

.download-link {
    display: inline-block;
    line-height: 24px;
    font-size: 16px;
    border-radius: 5px;
    background-color: #30A9DE;
    padding: 10px 20px;
    text-decoration: none;
    position: relative;
    color: $white;
    &--wrapper {
        text-align: center;
        padding: 20px 0;
    }
    &--file {
        &Size {
            font-size: 14px;
        }
    }
    &--message {
        position: absolute;
        top:50%;
        left: -30px;
        transform: translateY(-50%) translateX(-100%);
        color: #aaafc6;
    }
    span {
        display: block;
    }
    &:hover {
        background-color: #d6ecfa;
        color: darken(#30A9DE, 10);
    }
}
