/*----------  search-overlay
------------------------------------------------------------------------------*/

.searchOverlay {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 40;
    display: flex;
    flex-direction: row;
    width: 100%;
    height: 100%;
    background-color: rgba(255, 255, 255, .9);
    flex-wrap: wrap;
    cursor: url('../images/close.png') 22 22, auto;
    overflow-y: auto;
    &--inner {
        padding: 30px;
        cursor: default;
        max-width: 90%;
        margin: auto;
        & > * {
            margin: 10px;
            flex-grow: 1;
            flex-shrink: 1;
            flex-basis: 0;
        }
    }
    &--input,
    &--submit {
        padding: 15px 25px;
        border: 0;
        border-radius: 5px;
        font-size: 24px;
    }
    &--input {
        border: 1px solid $grey;
        max-width: 100%;
        @include breakpoint(phone) {
            font-size: 18px;
        }
    }
    &--submit {
        background-color: $green;
        color: $white;
        &:hover {
            background-color: darken($green, 10)
        }
        &:active {
            text-decoration: underline;
        }
    }
    &--tags {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        @include breakpoint(phone) {
            flex-direction: column;
            align-items: center;
            justify-content: center;
            line-height: 40px;
            font-size: 20px;
        }
        &Item {
            position: relative;
            color: #F68657;
            &:hover {
                &:before {
                    width: 100%;
                }
            }
            &:before {
                position: absolute;
                bottom: -1px;
                left: 0;
                display: block;
                width: 0;
                height: 2px;
                border-radius: 1px;
                background-color: currentColor;
                content: '';
                transition: width, .2s;
            }
        }
    }
}
