/*----------  layout
------------------------------------------------------------------------------*/
body,
html {
	height: 100%;
	width: 100%;
	@include font-smoothing(antialiased);
	-moz-osx-font-smoothing: grayscale;
}
body {
	font-size: 14px;
	overflow-x: hidden;
	background-color: $white;
	transition: filter, .5s;
	&.search-active {
		position: fixed;
		overflow-y: scroll;
		.container {
			filter: blur(2px) grayscale(100%);
		}
	}
}
.wrapper {
	margin: 0 auto;
	max-width: 940px;
	width: 100%;
	transition: padding 0.5s;
	@include breakpoint(1220px) {
		padding: 0 20px;
	}
}
.main {
	position: relative;
	background-color: $white;
}
