/*----------  article
------------------------------------------------------------------------------*/

.connected-posts {
    margin: 0px 0 20px;
    font-size: 18px;
    line-height: 36px;
    border-radius: 5px;
    display: inline-block;
    min-width: 50%;
    padding: 15px 20px;
    background-color: #eeeeee;
    ul, li, a {
        margin: 0;
        padding: 0;
    }
    a {
        color: #0099ff;
        &:hover {
            text-decoration: underline;
        }
    }
}
