/*----------  header
------------------------------------------------------------------------------*/

.header {
    width: 100%;
    @include breakpoint(390px) {
        padding-top: 60px;
    }
    .wrapper {
        border-bottom: 3px solid $greyLight;
        height: 80px;
        padding-top: 30px;
        @include breakpoint(390px) {
            height: 70px;
        }
    }
    &--logo {
        display: inline-block;
        position: fixed;
        top: 15px;
        left: 15px;
        h1 {
            @extend %visuallyhidden;
        }
        &Animation {
            background-image: url('../images/logo/logo-sprite.png');
            background-position: 0 0;
            background-repeat: no-repeat;
            display: block;
            width: 60px;
            height: 60px;
        }
    }
    &--social {
        display: inline-block;
        position: absolute;
        top: 30px;
        right: 30px;
        border-radius: 50%;
        box-shadow: 0 0px 0px 6px #45619d;
        background-color: #45619d;
        width: 30px;
        height: 30px;
        border-radius: 50%;
        @include breakpoint(1080px) {
            display: none;
        }
        a {
            display: block;
            background-image: url('../images/fb.png');
            background-size: cover;
            text-indent: -1999px;
            width: 30px;
            height: 30px;
            position: relative;
            &:hover {
                opacity: .9;
            }
            &:before {
                content: '';
                position: absolute;
                top: -6px;
                left: -6px;
                width: 42px;
                height: 42px;
                display: block;
                border-radius: 50%;
            }
        }
    }
    &--nav {
        float: right;
        display: inline-block;
        li {
            display: inline-block;
            &:not(:last-child) {
                margin-right: 25px;
                @include breakpoint(390px) {
                    margin-right: 10px;
                }
            }
        }
        a {
            color: $greyDark;
            font-weight: 600;
            font-size: 18px;
            &:hover {
                color: $blue;
            }
        }
    }
}
