/*----------  buttons
------------------------------------------------------------------------------*/
.btn {
    &.load-more {
        color: $white;
        background-color: $green;
        text-transform: uppercase;
        display: inline-block;
        margin: 30px auto 30px 60px;
        border-radius: 4px;
        text-align: center;
        padding: 10px 12px 11px;
        font-weight: 600;
        position: relative;
        &:hover {
            &:before {
                display: block;
            }
        }
        &:before {
            content: '';
            position: absolute;
            width: calc(100% - 24px);
            left: calc(50% - ((100% - 24px) / 2));
            bottom: 8px;
            height: 2px;
            background-color: $white;
            display: none;
        }
        &--loading {
            margin-right: auto;
            text-indent: -19999px;
            background: transparent;
            border-radius: 50%;
            width: 40px;
            height: 40px;
            border: 3px solid transparent;
            pointer-events: none;
            cursor: default;
            animation: rotate 4.5s infinite, toCenter 1s forwards;
        }
    }
}

@keyframes toCenter {
    0%   { margin-left: 0; }
    100% { margin-left: 50%; }
}

@keyframes rotate {
    0%   { border-top-color: $yellow; transform: rotate(0); }
    25%  { border-top-color: $blue; }
    50%  { border-right-color: #0000ff; }
    75%  { border-bottom-color: #7200da; }
    100% { border-left-color: #f9320c; transform: rotate(4.25turn);}
}
